import {AfterViewInit, Component} from '@angular/core';
import {fabric} from 'fabric';
import * as FontFaceObserver from 'fontfaceobserver';
import {Collisions} from 'detect-collisions';

enum State {
  Initial,
  Card,
  Running,
  After
}

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements AfterViewInit {
  title = 'happy-birthday';

  state = State.Initial;

  canvas: fabric.Canvas;
  background: fabric.Image;
  bee: fabric.Image;
  beeGroup: fabric.Group;
  flowerImage: HTMLImageElement;
  flowers: Array<fabric.Image> = [];
  wingDown: fabric.Image;
  wingUp: fabric.Image;
  signature: fabric.Image;
  scoreObject: fabric.Text;
  birthdayMessage: fabric.Text;
  restartButton: fabric.Rect;
  restartButtonText: fabric.Text;


  heldDown = false;
  startTime = 0;
  lastSpawnTime = 0;
  wingTimer = 0;
  lastFrameTime = 0;

  ngAfterViewInit(): void {
    const doneLoading = () => {
      numLoaded++;
      if (numLoaded === 8) {
        this.start();
      }
    };
    let numLoaded = 0;
    fabric.Image.fromURL('assets/background.png', loadedImage => {
      this.background = loadedImage;
      this.setupObject(this.background);
      this.background.scaleX = 0.5;
      this.background.scaleY = 0.5;
      doneLoading();
    });
    fabric.Image.fromURL('assets/bee.png', loadedImage => {
      this.bee = loadedImage;
      this.setupObject(this.bee);
      this.bee.scaleToWidth(100);
      doneLoading();
    });
    fabric.util.loadImage('assets/flower.png', imageElement => {
      this.flowerImage = imageElement;
      doneLoading();
    });
    fabric.Image.fromURL('assets/wing-down.png', loadedImage => {
      this.wingDown = loadedImage;
      this.setupObject(this.wingDown);
      this.wingDown.scaleToWidth(40);
      doneLoading();
    });
    fabric.Image.fromURL('assets/wing-up.png', loadedImage => {
      this.wingUp = loadedImage;
      this.setupObject(this.wingUp);
      this.wingUp.scaleToWidth(46);
      doneLoading();
    });
    fabric.Image.fromURL('assets/signature.png', loadedImage => {
      this.signature = loadedImage;
      this.setupObject(this.signature);
      this.signature.scaleToWidth(120);
      doneLoading();
    });

    ['Ballerina_Script', 'Octarine_Bold'].forEach(fontName => {
      const font = new FontFaceObserver(fontName);
      font.load().then(() => doneLoading());
    });
  }

  start(): void {
    this.canvas = new fabric.Canvas('the-canvas', {selection: false});
    this.canvas.setDimensions({width: this.background.getScaledWidth(), height: this.background.getScaledHeight()});
    this.scoreObject = new fabric.Text('', {
      fontSize: 36,
      originX: 'right',
      originY: 'top',
      left: this.canvas.getWidth() - 10,
      top: 10,
      fontFamily: 'Octarine_Bold',
      stroke: 'black',
      fill: '#FFB101',
    });
    this.setupObject(this.scoreObject);
    this.birthdayMessage = new fabric.Text('Happy Birthday Mom!', {
      fontSize: 72,
      originX: 'center',
      originY: 'center',
      left: this.canvas.getWidth() / 2,
      top: this.canvas.getHeight() / 2 - 100,
      fill: '#FFB101',
      width: this.canvas.getWidth(),
      height: 500,
      textAlign: 'center',
      stroke: 'black',
      strokeWidth: 0.5,
      fontFamily: 'Ballerina_Script'
    });
    this.restartButton = new fabric.Rect({
      originX: 'center',
      originY: 'center',
      left: this.canvas.getWidth() / 2,
      top: this.canvas.getHeight() / 2,
      width: 400,
      height: 80,
      fill: 'white',
      stroke: 'black',
      strokeWidth: 2,
      hoverCursor: 'pointer',
      selectable: false,
      rx: 4,
      ry: 4
    });
    this.restartButton.on('mousedown', () => {
      this.switchState(State.Running);
    });
    this.restartButtonText = new fabric.Text('Try Again?', {
      fontSize: 36,
      originX: 'center',
      originY: 'center',
      left: this.canvas.getWidth() / 2,
      top: this.canvas.getHeight() / 2,
      fontFamily: 'Octarine_Bold',
      width: 400,
      height: 80,
      textAlign: 'center',
      borderColor: 'black'
    });
    this.wingDown.set('left', 14);
    this.wingDown.set('top', 6);
    this.wingUp.set('left', 8);
    this.wingUp.set('top', -30);
    this.beeGroup = new fabric.Group([this.bee, this.wingDown, this.wingUp]);
    this.setupObject(this.beeGroup);
    this.setupObject(this.restartButtonText);
    this.setupObject(this.birthdayMessage);
    this.canvas.add(this.background);
    this.canvas.add(this.beeGroup);


    this.canvas.add(...this.flowers);
    this.canvas.add(this.scoreObject);
    this.canvas.add(this.birthdayMessage);
    this.canvas.add(this.signature);
    this.canvas.add(this.restartButton);
    this.canvas.add(this.restartButtonText);

    this.startTime = performance.now();

    this.canvas.on('before:render', () => {
      const currentTime = performance.now();
      switch (this.state) {
        case State.Card:
          break;
        case State.Running:
          this.beeGroup.set('top', this.beeGroup.get('top') + (this.heldDown ? -5 : 5));
          if (this.heldDown) {
            this.wingTimer += currentTime - this.lastFrameTime;
            const showDown = Math.round(this.wingTimer / 50) % 2 === 0;
            this.wingDown.set('visible', showDown);
            this.wingUp.set('visible', !showDown);
          } else {
            this.wingDown.set('visible', true);
            this.wingUp.set('visible', false);
            this.wingTimer = 0;
          }
          const angle = this.beeGroup.angle;
          if (this.heldDown) {
            this.beeGroup.set('angle', Math.max(-15, angle - 2));
          } else {
            this.beeGroup.set('angle', Math.min(15, angle + 2));
          }

          this.scoreObject.set('text', (Math.round((currentTime - this.startTime) / 100) * 100).toLocaleString(undefined));
          if (Math.random() < 0.02 && currentTime - this.lastSpawnTime > 2000) {
            this.lastSpawnTime = currentTime;
            const flower = new fabric.Image(this.flowerImage);
            this.setupObject(flower);
            flower.set('left', this.canvas.getWidth());
            flower.scaleX = 0.2;
            flower.scaleY = 0.4;
            if (Math.random() < 0.5) {
              flower.flipY = true;
              flower.top = Math.random() * -250;
            } else {
              flower.top = this.canvas.getHeight() - flower.getScaledHeight() + Math.random() * 250;
            }
            this.flowers.push(flower);
            this.canvas.add(flower);
          }
          const collisionCanvas = document.getElementById('collision-canvas') as HTMLCanvasElement;
          const ctx = collisionCanvas.getContext('2d');
          this.flowers = this.flowers.filter(flower => {
            const good = flower.left > -300;
            if (!good) {
              this.canvas.remove(flower);
            }
            return good;
          });
          this.beeGroup.setCoords();
          const system = new Collisions();
          const beeCenter = this.beeGroup.getCenterPoint();
          const beeBB = system.createCircle(beeCenter.x, beeCenter.y + 14, 25);
          this.flowers.forEach(flower => {
            flower.set('left', flower.get('left') - 2);
            flower.setCoords();

            const bb = flower.getBoundingRect(false);
            if (flower.flipY) {
              system.createCircle(bb.left + bb.width / 2, bb.top + bb.height - 50, 40);
              system.createPolygon(bb.left + bb.width / 2, bb.top + bb.height - 60, [[-10, 0], [10, 0], [10, -bb.height], [-10, -bb.height]]);
            } else {
              system.createCircle(bb.left + bb.width / 2, bb.top + 55, 40);
              system.createPolygon(bb.left + bb.width / 2, bb.top + 60, [[-10, 0], [10, 0], [10, bb.height], [-10, bb.height]]);
            }
          });
          system.createPolygon(0, 0, [[0, 0], [this.canvas.getWidth(), 0], [this.canvas.getWidth(), 2], [0, 2]]);
          system.createPolygon(0, this.canvas.getHeight(), [[0, 0], [this.canvas.getWidth(), 0], [this.canvas.getWidth(), 2], [0, 2]]);
          system.update();
          ctx.clearRect(0, 0, 800, 600);
          ctx.strokeStyle = '#FF0000';
          ctx.beginPath();
          system.draw(ctx);
          ctx.stroke();
          beeBB.potentials().forEach(potential => {
            if (beeBB.collides(potential)) {
              this.switchState(State.After);
            }
          });
          this.scoreObject.bringToFront();
          this.lastFrameTime = performance.now();
          break;
        case State.After:
          break;
      }
    });
    this.canvas.on('after:render', () => {
      this.canvas.requestRenderAll();
    });
    this.canvas.on('mouse:down', () => {
      this.heldDown = true;
      if (this.state === State.Card) {
        this.switchState(State.Running);
      }
    });
    this.canvas.on('mouse:up', () => {
      this.heldDown = false;
    });
    this.switchState(State.Card);
  }

  setupObject(object: fabric.Object): void {
    object.set('evented', false);
    object.set('selectable', false);
  }

  switchState(to: State): void {
    switch (to) {
      case State.Card:
        this.beeGroup.set('left', this.canvas.getWidth() / 2 - this.beeGroup.getScaledWidth() / 2);
        this.beeGroup.set('top', 320);

        this.signature.set('originX', 'right');
        this.signature.set('originY', 'bottom');
        this.signature.set('left', this.canvas.getWidth() - 10);
        this.signature.set('top', this.canvas.getHeight() - 10);
        this.restartButton.set('visible', false);
        this.restartButtonText.set('visible', false);
        this.wingDown.set('visible', true);
        this.wingUp.set('visible', false);
        break;
      case State.Running:
        this.canvas.remove(...this.flowers);
        this.flowers = [];
        this.beeGroup.set('left', 30);
        this.beeGroup.set('top', this.canvas.getHeight() / 2);
        this.birthdayMessage.set('visible', false);
        this.scoreObject.set('visible', true);
        this.startTime = performance.now();
        this.restartButton.set('visible', false);
        this.restartButtonText.set('visible', false);
        this.lastFrameTime = performance.now();
        this.wingTimer = 0;
        break;
      case State.After:
        this.restartButton.bringToFront();
        this.restartButtonText.bringToFront();
        this.restartButton.set('visible', true);
        this.restartButtonText.set('visible', true);
        break;
    }

    this.state = to;
  }
}
